    strInstructions:
      "Shift the cocoa and sugar together into a medium-sized saucepan. Dissolve the cornstarch in the water, and stir into the cocoa and sugar until it is a smooth paste. Begin heating the mixture, stirring it with a whisk, and gradually pour in the milk. Continue stirring with the whisk as you bring the liquid to a simmer. Allow the chocolate to simmer for about 10 minutes, stirring often, until it is thick, glossy and completely smooth. Serve steaming hot in coffee mug. Serves six.",
    strInstructionsES: null,
    strInstructionsDE:
      "Kakao und Zucker zusammen in einen mittelgro\u00dfen Topf geben. Die Maisst\u00e4rke im Wasser aufl\u00f6sen und in Kakao und Zucker einr\u00fchren, bis eine glatte Masse entsteht. Beginnen Sie mit dem Erw\u00e4rmen der Mischung, r\u00fchren Sie sie mit einem Schneebesen um und gie\u00dfen Sie die Milch langsam ein. Mit dem Schneebesen weiterr\u00fchren, w\u00e4hrend Sie die Fl\u00fcssigkeit zum Erhitzen bringen. Die Schokolade ca. 10 Minuten unter st\u00e4ndigem R\u00fchren k\u00f6cheln lassen, bis sie dick, gl\u00e4nzend und v\u00f6llig glatt ist. In der Kaffeetasse dampfend hei\u00df servieren. F\u00fcr sechs Personen.",
