    strInstructions:
      "Place the coffee and spices in the filter cone of your coffee maker. Brew coffee as usual, let it cool. In a tall glass, dissolve 1 or 2 teaspoons of sugar in an ounce of the coffee (it's easier to dissolve than if you put it right over ice). Add 5-6 ice cubes and pour coffee to within about 1 inch of the top of the glass. Rest a spoon on top of the coffee and slowly pour whipping cream into the spoon. This will make the cream float on top of the coffee rather than dispersing into it right away.",
    strInstructionsES: null,
    strInstructionsDE:
      "Geben Sie den Kaffee und die Gew\u00fcrze in den Filterkegel Ihrer Kaffeemaschine. Kaffee wie gewohnt zubereiten, abk\u00fchlen lassen. In einem hohen Glas 1 oder 2 Teel\u00f6ffel Zucker in 2 Essl\u00f6ffel Kaffee aufl\u00f6sen (es ist einfacher zu l\u00f6sen, als wenn man ihn direkt auf Eis legt). F\u00fcge 5-6 Eisw\u00fcrfel hinzu und gie\u00dfe Kaffee 2-3cm von der Oberseite des Glases auf. Einen L\u00f6ffel auf den Kaffee legen und langsam Schlagsahne in den L\u00f6ffel geben. Dadurch wird die Creme auf dem Kaffee schweben, anstatt sich sofort darin zu verteilen.",
