    strInstructions:
      "Combine all herbs, nuts and spices with vodka in a 1 quart or larger aging container. Cap tightly and shake daily for 2 weeks. Strain through a fine muslin cloth or coffee filter, discarding solids. Clean out aging container. Place liquid back in container. Place sugar and water in saucepan and stir to combine over medium heat. When sugar is completely dissolved, set aside and let cool. When cool combine with food coloring and add to liqueur liquid. Cap and allow to age and mellow in a cool, dark place for one month.",
    strInstructionsES: null,
    strInstructionsDE:
      "Vermengen Sie alle Kr\u00e4uter, N\u00fcsse und Gew\u00fcrze mit Wodka in einem 1 Liter, oder gr\u00f6\u00dferem Alterungsbeh\u00e4lter. Verschlie\u00dfen Sie das Produkt fest und sch\u00fctteln Sie es t\u00e4glich f\u00fcr 2 Wochen. Durch ein feines Musselintuch oder einen Kaffeefilter abseihen und dabei Feststoffe entfernen. Alterungsbeh\u00e4lter reinigen. Geben Sie die Fl\u00fcssigkeit wieder in den Beh\u00e4lter zur\u00fcck. Zucker und Wasser in einen Topf geben und bei mittlerer Hitze unter R\u00fchren vermengen. Wenn der Zucker vollst\u00e4ndig aufgel\u00f6st ist, beiseite stellen und abk\u00fchlen lassen. Nach dem Abk\u00fchlen mit der Lebensmittelfarbe mischen und in die Lik\u00f6rfl\u00fcssigkeit geben. Verschlie\u00dfen und einen Monat lang an einem k\u00fchlen, dunklen Ort lagern und reifen lassen.",
