    strGlass: "Collins Glass",
    strInstructions:
      "Boil water and sugar until dissolved. Turn off heat. Slowly add dry instant coffee and continue stirring. Add a chopped vanilla bean to the vodka, then combine the cooled sugar syrup and coffee solution with the vodka. Cover tightly and shake vigorously each day for 3 weeks. Strain and filter. Its also best to let the sugar mixture cool completely so the vodka won't evaporate when its added. If you like a smoother feel to the liqueur you can add about 1 teaspoon of glycerine to the finished product.",
    strInstructionsES: null,
    strInstructionsDE:
      "Wasser und Zucker kochen, bis sie gel\u00f6st sind. Schalten Sie die Heizung aus. Geben Sie langsam trockenen Instantkaffee hinzu und r\u00fchren Sie weiter. Dem Wodka eine gehackte Vanilleschote hinzuf\u00fcgen, dann den gek\u00fchlten Zuckersirup und die Kaffeel\u00f6sung mit dem Wodka vermengen. T\u00e4glich 3 Wochen lang fest abdecken und kr\u00e4ftig sch\u00fctteln. Abseihen und filtern. Am besten ist es auch, die Zuckermischung vollst\u00e4ndig abk\u00fchlen zu lassen, damit der Wodka nicht verdunstet, wenn er hinzugef\u00fcgt wird. Wenn Sie einen weicheren Geschmack f\u00fcr den Lik\u00f6r m\u00f6gen, k\u00f6nnen Sie dem Endprodukt etwa 1 Teel\u00f6ffel Glyzerin hinzuf\u00fcgen.",
