    strGlass: "Collins Glass",
    strInstructions:
      "Combine all ingreds in aging container. Cover tightly and shake gently several times during the first 24 hrs. After 24 hrs, remove the lemon zest. Cover again and let stand in a cool, dark place for 2 weeks, shaking gently every other day. Strain through a wire sieve to remove the angelica root and fennel. Return to aging container, cover and let stand undisturbed in a cool dark place for 6 months. Siphon or pour clear liqueur into a sterile bottle. The cloudy dregs may be saved for cooking.",
    strInstructionsES: null,
    strInstructionsDE:
      "Alle Zutaten in einem Alterungsbeh\u00e4lter zusammenf\u00fcgen. In den ersten 24 Stunden fest abdecken und mehrmals leicht sch\u00fctteln. Nach 24 Stunden die Zitronenschale entfernen. Nochmals zugedeckt an einem k\u00fchlen, dunklen Ort f\u00fcr 2 Wochen stehen lassen und jeden zweiten Tag sanft sch\u00fctteln. Durch ein Drahtsieb sieben, um die Angelikawurzel und den Fenchel zu entfernen. In den Alterungsbeh\u00e4lter zur\u00fcckgegeben, abdecken und 6 Monate ungest\u00f6rt an einem k\u00fchlen, dunklen Ort stehen lassen. Siphonierten oder klaren Lik\u00f6r in eine sterile Flasche gie\u00dfen. Die Tr\u00fcbstoffe k\u00f6nnen zum Kochen aufbewahrt werden.",
