    strIBA: null,
    strAlcoholic: "Optional alcohol",
    strGlass: "Punch bowl",
    strInstructions:
      "In a small mixer bowl beat egg yolks till blended. Gradually add 1/4 cup sugar, beating at high speed till thick and lemon colored. Stir in milk, stir in rum, bourbon, vanilla, and salt. Chill thoroughly. Whip cream. Wash beaters well. In a large mixer bowl beat egg whites till soft peaks form. Gradually add remaining 1/4 cup sugar, beating to stiff peaks. Fold yolk mixture and whipped cream into egg whites. Serve immediately. Sprinkle nutmeg over each serving. Serve in a punch bowl or another big bowl. NOTE: For a nonalcoholic eggnog, prepare Eggnog as above, except omit the bourbon and rum and increase the milk to 3 cups.",
    strInstructionsES: null,
    strInstructionsDE:
      "In einer kleinen R\u00fchrsch\u00fcssel das Eigelb verr\u00fchren. Nach und nach 1/4 Tasse Zucker hinzuf\u00fcgen und mit hoher Geschwindigkeit schlagen, bis er dick und zitronengelb ist. Milch unterr\u00fchren, Rum, Bourbon, Vanille und Salz unterr\u00fchren. Gr\u00fcndlich abk\u00fchlen lassen. Sahne schlagen. Schl\u00e4gel gut waschen. In einer gro\u00dfen Mixersch\u00fcssel Eiwei\u00df zu weichen Spitzen schlagen. Nach und nach den restlichen Zucker aus der 1/4 Tasse hinzuf\u00fcgen und zu steifen Spitzen schlagen. Eigelbmischung und Schlagsahne zu Eiwei\u00df vermengen. Sofort servieren. Muskatnuss \u00fcber jede Portion streuen. In einer Bowle oder einer anderen gro\u00dfen Sch\u00fcssel servieren. HINWEIS: F\u00fcr einen alkoholfreien Eierlik\u00f6r bereiten Sie den Eierlik\u00f6r wie oben beschrieben zu, Bourbon und Rum weglassen und die Milch auf 3 Tassen erh\u00f6hen.",
