    strInstructions:
      "In large saucepan, beat together eggs, sugar and salt, if desired. Stir in 2 cups of the milk. Cook over low heat, stirring constantly, until mixture is thick enough to coat a metal spoon and reaches 160 degrees F. Remove from heat. Stir in remaining 2 cups milk and vanilla. Cover and regfigerate until thoroughly chilled, several hours or overnight. Just before serving, pour into bowl or pitcher. Garnish or add stir-ins, if desired. Choose 1 or several of: Chocolate curls, cinnamon sticks, extracts of flavorings, flavored brandy or liqueur, fruit juice or nectar, ground nutmeg, maraschino cherries, orange slices, peppermint sticks or candy canes, plain brandy, run or whiskey, sherbet or ice-cream, whipping cream, whipped. Serve immediately.",
    strInstructionsES: null,
    strInstructionsDE:
      "In einem gro\u00dfen Topf Eier, Zucker und Salz verquirlen. 2 Tassen der Milch unterr\u00fchren. Bei schwacher Hitze unter st\u00e4ndigem R\u00fchren kochen, bis die Mischung dick genug ist, um einen Metalll\u00f6ffel zu \u00fcberziehen und 70 Grad Celsius erreicht. Vom Herd nehmen. Die restlichen 2 Tassen Milch und Vanille unterr\u00fchren. Zugedeckt und gek\u00fchlt bis zur vollst\u00e4ndigen Abk\u00fchlung, mehrere Stunden oder \u00fcber Nacht. Kurz vor dem Servieren in eine Sch\u00fcssel oder einen Krug geben. Garnieren oder erg\u00e4nzen Sie Mischung, wenn gew\u00fcnscht. W\u00e4hlen Sie 1 oder mehrere: Mit Schoko-Curls, Zimtstangen, Aromaextrakte, aromatisierter Brandy oder Lik\u00f6r, Fruchtsaft oder Nektar, gemahlene Muskatnuss, Maraschinokirschen, Orangenscheiben, Pfefferminzstangen oder Zuckerstangen, schlichter Brandy, Rum oder Whiskey, Sorbet oder Eis, Schlagsahne, aufschlagen. Sofort servieren.",
