    strAlcoholic: "Alcoholic",
    strGlass: "Shot glass",
    strInstructions:
      "1. Pour Kahlua into shot glass to about 1/2 full. 2. Using a spoon(inverted), slowly pour in the Peppermint Schnapps until glass is about 3/4 full. Done correctly, the Schnapps will flow under the Kahlua for a clear layer. 3. Again using a spoon, but this time right side up, slowly top off the glass with a layer of Bailey's. Be careful to place the spoon right at the top of the Kahlua layer and to raise it as the glass fills up. Done correctly, this will provide a layer of Bailey's floating over the Kahlua. 4. Toss it down all at once for something like a Peppermint Pattie, WITH A BANG!!! NOTE: Best if all ingredients are chilled",
    strInstructionsES: null,
    strInstructionsDE:
      "1. Gie\u00dfen Sie Kahlua in das Schnapsglas bis etwa 1/2 voll. 2. Mit einem L\u00f6ffel (invertiert) den Pfefferminzlik\u00f6r langsam einf\u00fcllen, bis das Glas etwa 3/4 voll ist. Richtig gemacht, flie\u00dft der Schnaps unter dem Kahlua f\u00fcr eine klare Schicht. 3. Wieder mit einem L\u00f6ffel, aber diesmal mit der rechten Seite nach oben, das Glas langsam mit einer Schicht Bailey's abdecken. Achten Sie darauf, den L\u00f6ffel ganz oben auf die Kahluaschicht zu stellen und ihn beim Auff\u00fcllen des Glases anzuheben. Richtig gemacht, wird dies eine Schicht von Bailey's, die \u00fcber dem Kahlua schwimmt, ergeben. 4. Trinken Sie es auf einmal weg, mit einem Knall. HINWEIS: Am besten, wenn alle Zutaten gek\u00fchlt sind.",
